*{
  box-sizing: border-box;
  margin:0;
  padding: 0;
}

nav{
  background-color: black;
  padding: 20px;
  color: white;
}

.ul-head{
  display: flex;
  justify-content: space-between;
  align-items: center;
 
}
.ul-head>li{
  list-style: none;
 
  color: white;
  padding:5px;
  border-radius: 9px;
}
a{
   text-decoration: none;
   color: white;
}
a:hover{
  border: 2px black solid;
  border-radius:5px;
  padding-left: 5px;
  padding-right: 5px;
}

.form-body{
  text-align: center;
  margin:auto;
  margin-top:200px;
  border: 1px rgba(116, 110, 110, 0.84) solid;
  max-width: 400px;
  padding: 50px;
  display: block;
}
.btn.btn-primary{
  width: 100%;
  padding: 8px;
  background-color: black;
  color: white;
  border: 0;
  border-radius: 10px;
  cursor: pointer;
}
.form-body>div{
   margin:10px;
}
input{
  width: 100%;
  padding: 10px;
}
h1:hover{
  background-color:black;
}
.App>.container{
  padding: 20px;
  display: grid;
  gap: 10px;
  grid-template-columns: 50% 50% ;
}
.none{
  display: none;
}